:root {
  --color-primary: #fff;
  --color-secondary: #1a8ad9;
}

.container {
  max-width: 90vw;
  margin: 0 auto;
  padding: 0 15px;
}

.skills {
  &__title {
    color: var(--color-primary);
  }
  &__intro {
    display: flex;
    align-items: center;
    margin-top: 20px;
    justify-content: space-between;
  }
  &__item {
    align-items: center;
    column-gap: 150px;
    row-gap: 50px;
    flex-wrap: wrap;
    padding: 30px 0;
    width: 40%;
    display: flex;
    justify-content: space-between;
  }
  &__icon {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    transition: all 0.9s ease;
    width: 90px;
    color: var(--color-primary);
    &:hover {
      color: var(--color-secondary);
    }
    span {
      font-size: 24px;
      font-weight: 500;
      opacity: 0;
      transition: all 0.9s ease;
      &:hover {
        color: var(--color-secondary);
      }
    }
    &:hover span {
      opacity: 1;
    }
  }
  &__avatar img {
    width: 100%;
    height: auto;
    margin: 2px;
    max-width: 500px;
  }
}
@media (min-width: 1880px) {
  .container {
    max-width: 1660px;
  }
}

@media (max-width: 767px) {
  .skills {
    &__intro {
      flex-direction: column;
    }
    &__item {
      width: 80%;
      justify-content: center;
    }
  }
}
