:root {
  --color-primary: #fff;
  --color-secondary: #1a8ad9;
  --color-bg-menu: rgba(119, 137, 172, 0.9);
}

.container {
  max-width: 90vw;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  position: relative;
  &__bg img {
    position: absolute;
    width: 100%;
    height: auto;
    max-width: 314px;
    left: -160px;
    top: -40px;
    transform: rotate(315deg);
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 15px;
    align-items: center;
  }
  &__title a {
    color: var(--color-primary);
    cursor: pointer;
    font-size: 36px;
    font-weight: 600;
  }
  &__navbar {
    display: flex;
    justify-content: space-between;
    width: 60%;
  }
  &__link {
    color: var(--color-primary);
    font-weight: 400;
    font-size: 20px;
    cursor: pointer;
    position: relative;
    transition: all 900ms ease 0s;
  }
  &__link:hover {
    color: var(--color-secondary);
    transition: all 900ms ease 0s;
  }
  &__link:hover::before {
    transform: scaleX(1);
    transition: all 900ms ease 0s;
  }
  &__link::before {
    content: "";
    width: 100%;
    height: 2px;
    background: var(--color-secondary);
    position: absolute;
    left: 0;
    bottom: -35px;
    transform: scaleX(0);
    transition: all 900ms ease 0s;
  }
  &__icon {
    display: none;
  }
}

@media (max-width: 797px) {
  .container {
    max-width: none;
  }
  .header {
    top: 0;
    width: 100%;
    &__title {
      margin-left: 20px;
    }
    &__navbar {
      position: fixed;
      top: 0;
      left: -100%;
      width: 100vw;
      flex-direction: column;
      align-items: center;
      height: 100%;
      justify-content: space-around;
      background: var(--color-bg-menu);
      transition: left 900ms ease 0s;
      z-index: 10;
    }

    &__navbar._active {
      left: 0%;
    }
    &__icon {
      z-index: 10;
      display: block;
      position: relative;
      width: 30px;
      height: 18px;
      cursor: pointer;
      top: 0;
    }
    &__icon span,
    &__icon::before,
    &__icon::after {
      left: 0;
      position: absolute;
      height: 10%;
      width: 100%;
      transition: all 900ms ease 0s;
      background-color: var(--color-primary);
    }
    &__icon::before,
    &__icon::after {
      content: "";
    }
    &__icon::before {
      top: 0;
    }
    &__icon::after {
      bottom: 0;
    }
    &__icon span {
      top: 50%;
      transform: scale(1) translate(0px, -50%);
    }
    &__icon._active span {
      transform: scale(0) translate(0, -50%);
    }
    &__icon._active::before {
      top: 50%;
      transform: rotate(-45deg) translate(0, -50%);
    }
    &__icon._active::after {
      bottom: 50%;
      transform: rotate(45deg) translate(0, 50%);
    }
  }
}
